import Vue from 'vue';
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        Email: '',
        APIURL: 'http://127.0.0.1:3000',
        Code: ''
    },
    mutations: {
        SetEmail(state, Email) {
            state.Email = Email;
        },
        SetCode(state, Code) {
            state.Code = Code;
        }
    }
})

export default store
