import Vue from 'vue'
import Router from 'vue-router'
import SettingNewPassword from "@/components/SettingNewPassword";
import RegisteredEmail from "@/components/RegisteredEmail";
import VerificationCode from "@/components/VerificationCode";

Vue.use(Router);


export default new Router({
    //TODO:指定发布后的项目目录
    mode:'history',
    base:'/fogotpassword',
    routes: [
        {
            path: '/',
            component: RegisteredEmail
        },
        {
            path: '/VerificationCode',
            component: VerificationCode
        },
        {
            path: '/SettingNewPassword',
            component: SettingNewPassword
        }

    ]
})
