<template>
    <el-card class="boxCard_SettingPWD">
        <span id='title_SettingPWD'><strong>找回密码</strong></span>
        <div id="tip_SettingPWD">验证码已发送至你的注册邮箱</div>
        <el-input v-model="Email" disabled></el-input>
        <div class="GetCode_VerificationCode">
            <el-input placeholder="输入6位邮箱验证码" v-model="Code"></el-input>
            <el-button @click="GetEmailVerification" style="width: 30%" type="success" :disabled="disabled=Is60s">
                <span v-show="!Is60s">获取验证码</span>
                <span v-show="Is60s">{{count}} s</span>
            </el-button>
        </div>
        <el-button type="primary" @click="Next">下一步</el-button>
    </el-card>
</template>

<script>
    import axios from "axios";

    export default {
        data() {
            return {
                Code: '',
                Is60s: false,
                count: '60',
                timer: null
            }
        },
        name: 'VerificationCode',
        props: {
            msg: String
        },
        computed: {
            Email() {
                return this.$store.state.Email;
            },
            APIURL() {
                return this.$store.state.APIURL;
            }
        },
        methods: {
            Send: function () {
                this.Is60s = true;
                if (!this.timer) {
                    this.count = 60;
                    this.show = false;
                    this.timer = setInterval(() => {
                        if (this.count > 0 && this.count <= 60) {
                            this.count--;
                        } else {
                            this.Is60s = false;
                            clearInterval(this.timer);  // 清除定时器
                            this.timer = null;
                        }
                    }, 1000)
                }
            },
            Next: function () {
                let Self = this;
                let param = {'email': Self.Email, 'code': Self.Code};
                console.log(Self.Email);
                axios({
                    method: 'get',
                    url: Self.APIURL + '/users/verification_code',
                    params: param,
                    // headers: {
                    //     'Content-Type': 'application/x-www-form-urlencoded',
                    // }
                }).then(function (res) {
                    //TODO:如果服务端验证(验证码)正确，将验证码存储到vuex中
                    if (res.data.success == '200') {
                        //进入下一个修改密码页面
                        Self.$router.push('/SettingNewPassword');
                        Self.$store.commit('SetCode', Self.Code);
                    } else {
                        Self.$message.error(res.data.message);
                    }

                });
            }

            ,
            GetEmailVerification: function () {
                this.Send();
                let Self = this;
                let param = new URLSearchParams();
                param.append('email', Self.Email);
                axios({
                    method: 'post',
                    url: Self.APIURL + '/users/verification_code',
                    data: param,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded',
                    }
                }).then(function (res) {
                    if (res.data.success == '200') {
                        Self.$message.success(res.data.message);
                        Self.$router.push('/VerificationCode');
                        Self.$store.commit('SetEmail', Self.Email);
                    } else {
                        Self.$message.error(res.data.message);
                    }

                });
            }
        }
        ,
        mounted() {
            this.Send();
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
    .el-card {
        width: 380px;
        margin: 160px auto;
        padding: 10px;

        .el-button {
            display: block;
            width: 100%;
        }

        #title_SettingPWD {
            font-size: 40px;
            float: left;
        }

        #tip_SettingPWD {
            clear: left;
            display: block;
            text-align: left;
            color: #878fa4;
            margin-bottom: 50px;
        }

        .el-input {
            height: 60px;
            line-height: 60px;
        }

        .el-button {
            margin-top: 20px;
        }

        .GetCode_VerificationCode {
            .el-input {
                display: inline-block;
                left: 0px;
                width: 60%;
                margin-right: 30px;

            }

            .el-button {
                display: inline-block;
            }
        }
    }
</style>
