<template>
    <el-card class="boxCard_SettingPWD">
        <span id='title_SettingPWD'><strong>找回密码</strong></span>
        <div id="tip_SettingPWD">验证码将会发送至你的注册邮箱</div>
        <el-input placeholder="你的注册邮箱" v-model="Email"></el-input>
        <el-button type="primary" @click="GetEmailVerification">下一步</el-button>
    </el-card>
</template>

<script>
    import axios from 'axios'
    export default {
        data() {
            return {
                Email: '',
            }
        },
        name: 'RegisteredEmail',
        props: {
            msg: String
        },
        methods: {
            GetEmailVerification: function () {
                let Self = this;
                let param = new URLSearchParams();
                param.append('email', Self.Email);
                axios({
                    method: 'post',
                    url: Self.APIURL + '/users/verification_code',
                    data: param,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded',
                    }
                }).then(function (res) {
                    if (res.data.success == '200') {
                        Self.$message.success(res.data.message);
                        Self.$router.push('/VerificationCode');
                        Self.$store.commit('SetEmail', Self.Email);
                    } else {
                        Self.$message.error(res.data.message);
                    }

                });


                // let params=new URLSearchParams();
                // params.append('email','12339338@qq.com');
                // axios.post(this.APIURL+'/verification_code',params).then((res)=>{
                //     console.log(res);
                // })
            }
        },
        computed: {
            APIURL() {
                return this.$store.state.APIURL;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
    .el-card {
        width: 380px;
        margin: 160px auto;
        padding: 10px;

        .el-button {
            display: block;
            width: 100%;
        }

        #title_SettingPWD {
            font-size: 40px;
            float: left;
        }

        #tip_SettingPWD {
            clear: left;
            display: block;
            text-align: left;
            color: #878fa4;
            margin-bottom: 50px;
        }

        .el-input {
            height: 60px;
            line-height: 60px;
        }

        .el-button {
            margin-top: 20px;
        }
    }
</style>
