<template>
    <el-card class="boxCard_SettingPWD">
        <span id='title_SettingPWD'><strong>设置新密码</strong></span>
        <div id="tip_SettingPWD">新密码应不少于8位，且不可与之前设置过的密码重复。</div>

        <el-form label-width="0" class="login-form" :model="PasswordForm" :rules="rules" ref="PasswordFormRef">
            <el-form-item prop="password1">
                <el-input placeholder="新密码" v-model="PasswordForm.password1" show-password
                          :validate-event="false"></el-input>
            </el-form-item>
            <el-form-item prop="password2">
                <el-input placeholder="确认新密码" v-model="PasswordForm.password2" show-password
                          :validate-event="false"></el-input>
            </el-form-item>
            <el-button type="primary" @click="ResetPassword">重置密码</el-button>
        </el-form>

    </el-card>
</template>

<script>
    import axios from "axios";

    export default {
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.PasswordForm.password1) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                PasswordForm: {
                    password1: '',
                    password2: ''
                },
                rules: {
                    password1: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        {min: 8, max: 15, message: '长度在8-15个字符', trigger: 'blur'}
                    ],
                    password2: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        {validator: validatePass, trigger: 'blur'}
                    ]
                }

            }
        },
        name: 'SettingNewPassword',
        props: {
            msg: String
        },
        computed: {
            APIURL() {
                return this.$store.state.APIURL;
            },
            Email() {
                return this.$store.state.Email;
            },
            Code() {
                return this.$store.state.Code;
            }
        },
        methods: {
            ResetPassword: function () {
                let Self = this;
                this.$refs.PasswordFormRef.validate((valid) => {
                    if (valid) {
                        //TODO:发送重置密码的请求
                        let param = new URLSearchParams();
                        param.append('email', Self.Email);
                        param.append('code', Self.Code);
                        param.append('newPassword', Self.PasswordForm.password1);
                        console.log(param);
                        axios({
                            method: 'post',
                            url: Self.APIURL + '/users/updatePassword',
                            data: param,
                            headers: {
                                'Content-Type': 'application/x-www-form-urlencoded',
                            }
                        }).then(function (res) {
                            console.log(res);
                            if (res.data.success == '200') {
                                Self.$message.success('重置密码成功,2秒后返回主页!');
                                setTimeout(()=>{
                                    window.location.href="http://127.0.0.1:3000/login"
                                },2000)

                                //
                                // Self.$message.success(res.data.message);
                                // Self.$router.push('/VerificationCode');
                                // Self.$store.commit('SetEmail', Self.Email);
                            } else {
                                Self.$message.error(res.data.message);
                            }
                        });
                    } else {
                        console.log('本地验证未通过');
                    }
                })
                return;
            }
        }
    }
</script>

